}

.waterfall .waterfall-item .gallary-overlay {
    background: rgba(52, 58, 64, 0.7);
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: -ms-flex;
    display: -webkit-box;
